* {
    margin: 0 0;
    padding: 0 0;
    box-sizing: border-box;
    text-decoration: none;
    background: transparent;
    font-family: "Poppins", sans-serif;
    background-color: white;
}

body {
    overflow: hidden;
    overflow-y: auto;
}

.clsHeader {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
    flex-direction: column;
}

.clsHeaderTitle {
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 2rem;
    padding: 1rem 1rem;
}

.clsAddTitle {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;
}

.clsAddTitleLabel {
    display: flex;
    font-size: 1em;
    padding: 0.3rem 0.3rem;
    font-weight: 400;
    align-self: start;
}

.clsAddTitleInput {
    width: 20rem;
    height: 3rem;
    border: 0.1rem solid black;
    border-radius: 0.23em;
    padding: 0.3rem 0.3rem;
    font-size: 1.4em;
    font-weight: 300;
    margin-bottom: 1.5rem;
}

.clsAddDocsBtn.dx-button {
    color: black;
    border: 0.1rem solid black;
}

.clsAddDocsBtn.dx-state-hover {
    background-color: white !important;
    border: 0.1rem solid black !important;
}

.clsAddDocs.dx-button {
    color: black;
    border: 0.10rem solid black;

}

.clsAddDocs.dx-state-hover {
    background-color: white !important;
    border: 0.10rem solid black !important;
}

.clsDocumentCnt {
    width: 20rem;
    height: 10rem;
    color: whitesmoke;
    border: 0.1rem solid black;
    border-radius: 1rem;
    margin: 1rem 1rem;
}

.clsDocumentHeaderInner {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    margin: 1rem 1rem;
}

.clsEditBtn {
    color: black;
}

.clsDeleteBtn {
    color: black;
}

.clsDocHeaderTitle {
    color: black;
    margin-right: 4em;
    border: none;
    font-size: 1rem;

}

.cntDocsALignment {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

}

.clsDocsTextarea {
    border: none;
    font-family: Georgia, 'Times New Roman', Times, serif;
    resize: none;
    margin-left: 1vh;
    font-size: small;
    overflow: hidden
}

.clsQuill {
    max-width: 97%;
    max-width: 100%;
    align-items: center;
    padding: 1rem 1rem;
    margin: 1rem 1rem;
    border: "2px solid gray";
}

.clsDocsHeader {
    display: flex;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 20%;
    align-self: center;
    justify-content: center;
    padding: 1rem 1rem;
}

.clsNotFound {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.1rem solid black;
    width: 50%;
    height: 50%;
    flex-direction: row;
    border-radius: 1rem;
    background-color: whitesmoke;
    color: black;
    margin-top: 25%;
    margin-left: 25%;
}

.clsNotFoundTxt {
    background-color: whitesmoke;
    font-size: 10%;
}

.clsSaveBtn.dx-button {
    width: 10rem;
    margin-left: 50% !important;
    margin-bottom: 5%;
    border: 0.1rem solid black;
}

.clsSaveBtn.dx-state-hover {
    background-color: white !important;
    border: 0.1rem solid black !important;
}

.clsExmptyDocument {
    margin-top: 5%;
}